.App {
  text-align: center;
}

header {
  margin: 1% 0 0 0;
  padding-bottom: 1%;
  display: flex;
  justify-content: flex-start;
  gap: 2vw;
  border-bottom: 2px solid gray;
  background-color: #FFF;
}

header img {
  height: 10vh;
  padding: 0 2vw 0 4vw;
}

header a {
  flex-basis: 15%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

main {
  margin-top: -22px;
  background-color: #FAFAFA;
}